import React, { PureComponent } from 'react';
import styled, { keyframes } from 'styled-components';
import scrollTo from '../lib/scroll-into-view';
import { toggleNavigationVisibility } from '../lib/navbar';

// components
import { Button } from './inputs';

// assets
import bgImage from '../images/bg-optimized.jpg';
import logoLeft from '../images/jk-logo-white.svg';
import logoRight from '../images/bracket.svg';

/**
 * Note: all of these styles are the "mobile" styles
 * TODO: learn more about styled component themes and variables and fun stuff like that
 */
const Container = styled.header`
  background-image: url(${bgImage});
  background-position: center center;
  background-size: cover;
  height: 100vh;
`;

const logoLeftAnimationMobile = keyframes`
  0% {
      opacity: 0;
      transform: translate3d(0, -70px, 0);
  }
  50% {
      opacity: 1;
      transform: none;
  }
  100% {
      opacity: 1;
      transform: translate3d(-96px, 0, 0);
  }
`;

const logoLeftAnimationSmall = keyframes`
  0% {
    opacity: 0;
    transform: translate3d(0, -270px, 0);
  }
  50% {
    opacity: 1;
    transform: none;
  }
  100% {
    opacity: 1;
    transform: translate3d(-204px, 0, 0);
  }
`;

const LogoLeft = styled.img`
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  top: 200px;
  width: 42px;
  height: 45px;

  animation-duration: 2s;
  animation-fill-mode: both;
  animation-name: ${logoLeftAnimationMobile};

  @media (min-width: 768px) {
    top: 309px;
    width: 84px;
    height: 90px;
    animation-name: ${logoLeftAnimationSmall};
  }
`;

const logoRightAnimationMobile = keyframes`
  0% {
      opacity: 0;
      transform: none;
  }
  50% {
      opacity: 0;
      transform: none;
  }
  100% {
      opacity: 1;
      transform: translate3d(114px, 0, 0);
  }
`;

const logoRightAnimationSmall = keyframes`
  0% {
    opacity: 0;
    transform: none;
  }
  50% {
    opacity: 0;
    transform: none;
  }
  100% {
    opacity: 1;
    transform: translate3d(224px, 0, 0);
  }
`;

// TODO: is this extra styling necessary?
const LogoRight = styled.img`
  position: absolute;
  left: -38px;
  right: 0;
  margin: 0 auto;
  top: 199px;
  width: 42px;
  height: 46px;

  animation-duration: 2s;
  animation-fill-mode: both;
  animation-name: ${logoRightAnimationMobile};

  @media (min-width: 768px) {
    top: 308px;
    width: 84px;
    height: 90px;
    animation-name: ${logoRightAnimationSmall};
  }
`;

// TODO: DRY
const typeAnimation = keyframes`
  0% {
    opacity: 0;
    width: 0;
  }
  50% {
    opacity: 1;
    width: 0;
  }
  100% {
    opacity: 1;
    width: 100;
  }
`;

const Title = styled.h1`
  opacity: 0;
  position: absolute;
  color: #fff;
  font-family: 'Lato', sans-serif;
  font-size: 18px;
  white-space: nowrap;
  overflow: hidden;
  width: 150px;
  display: block;
  top: 212px;
  left: 50%;
  right: 0;
  margin: 0 0 0 -75px;
  font-weight: 500;

  animation: ${typeAnimation} 2s steps(30, end);
  animation-delay: 0.75s;
  animation-fill-mode: both;

  @media (min-width: 768px) {
    opacity: 0;
    font-size: 38px;
    font-weight: bold;
    width: 318px;
    top: 326px;
    margin: 0 0 0 -159px;
  }
`;

// TODO: DRY
const blinker = keyframes`
  to {
    opacity: .0;
  }
`;

const Blink = styled.span`
  margin: 0 0 0 4px;
  animation: ${blinker} 1.25s infinite;
`;

class Intro extends PureComponent {
  render() {
    const { metaData, title } = this.props;
    return (
      <Container id="intro">
        {/* TOOD: id is for js to manipulate this DOM node, perhaps it's time to use react for this? */}
        <LogoLeft src={logoLeft} alt={metaData.logoLeft} />
        {/* TODO: decouple Title and Blink and make it reusable for the nav version... */}
        <Title>
          {title}
          <Blink>|</Blink>
        </Title>
        <LogoRight src={logoRight} alt={metaData.logoRight} />
        {/* TODO: this button is not yet functional */}
        <Button
          href="#about"
          type="go"
          onClick={e => {
            e.preventDefault();
            scrollTo('about', 800, true, () =>
              toggleNavigationVisibility(true)
            );
          }}
        >
          Go
        </Button>
      </Container>
    );
  }
}

export default Intro;
