import React from "react"
import { Container, Row, Col } from "react-bootstrap"

const About = ({ body }) => (
  <div id="about">
    <Container>
      <Row>
        <Col lg={{ span: 10, offset: 1 }} xs={12}>
          <p dangerouslySetInnerHTML={{ __html: body }} />
        </Col>
      </Row>
    </Container>
  </div>
)

export default About
